import React, { useState } from "react";
import { Modal, Button, Form } from "react-bootstrap";
import { assignedCard } from "../actions/cardAction";

const CardModal = ({ visitorId }) => {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [cardNo, setCardNo] = useState("");

  const submitHandler = async (e) => {
    e.preventDefault();
    try {
      await assignedCard(cardNo, visitorId).then((response) => {
        handleClose();
        window.location.reload();
      });
    } catch (error) {
      //history.push("/newrequest", );
    }
  };

  return (
    <>
      <Button variant="outline-info" onClick={handleShow}>
        Assign Card
      </Button>

      <Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Assign Card To Visitor</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={submitHandler}>
            <Form.Group controlId="cardNo">
              <Form.Label>Visitor Mobile No</Form.Label>
              <Form.Control
                type="number"
                placeholder="Enter Card No"
                value={cardNo}
                onChange={(e) => setCardNo(e.target.value)}
              />
            </Form.Group>

            <Button type="submit" variant="success">
              Assigned
            </Button>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          {/* <Button variant="primary">Understood</Button> */}
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default CardModal;
