import React, { useState, useEffect } from "react";
import { Container, Row, Col, Form, Button } from "react-bootstrap";
import { createUser } from "../actions/userAction";
import {
  createAppointment,
  getOfficerName,
} from "../actions/AppointmentAction";

const CreateNewRequestScreen = (visitor) => {
  
  const [visitorInfo] = useState(visitor.location.state);

  const [visitorName, setVisitorName] = useState("");
  const [mobile, setMobile] = useState(visitor.match.params.mobile);
  const [fatherName, setFatherName] = useState("");
  const [nid, setNid] = useState("");
  const [address, setAddress] = useState("");
  const [profession, setProfession] = useState("");
  const [password] = useState("1234");

  const [officer, setofficerName] = useState();
  const [appointmentDate, setAppointmentdate] = useState("");
  const [appointmentTime, setAppointmentTime] = useState("");
  const [priority] = useState("High");
  const [purpose, setPurpose] = useState("");
  const [details, setDetails] = useState("");
  const [officers, setOfficers] = useState([]);

  useEffect(() => {
    if (visitorInfo) {
      setVisitorName(visitorInfo.visitorName);
      setMobile(visitorInfo.mobile);
      setFatherName(visitorInfo.fatherName);
      setNid(visitorInfo.nid);
      setAddress(visitorInfo.address);
      setProfession(visitorInfo.profession);
    }

    const listU = async () => {
      const { data } = await getOfficerName();
      setOfficers(data);
    };
    listU();
  }, [visitorInfo]);
  //  const [uploading, setUploading] = useState(false);

  // const uploadFileHandler = (e) => {
  //   uploadFile(e, setAddress, setUploading);
  // };
  const appointmentHandler = (e, id) => {
    e.preventDefault();
    console.log("creating....");
    createAppointment({
      visitor: id,
      officer,
      date: appointmentDate,
      time: appointmentTime,
      priority,
      purpose,
      details,
    }).then(() => {
      visitor.history.push("/sortedAppointment/Pending");
    });
  };

  const createUserHandler = (e) => {
    e.preventDefault();
    if (window.confirm("Are you sure to Submit ?")) {
      if (!visitorInfo) {
        createUser(
          {
            visitorName,
            mobile,
            fatherName,
            nid,
            address,
            profession,
            password,
          },
          "visitor"
        ).then((response) => {
          appointmentHandler(e, response.data._id);
        });
      } else {
        appointmentHandler(e, visitorInfo._id);
      }
    }
  };

  if (!visitorInfo) {
  }

  return (
    <div>
      <Container>
        <Form onSubmit={createUserHandler}>
          <Row>
            <Col>
              <h2>Visitor Details</h2>
              <Form.Group>
                <Form.Group as={Row} controlId="visitorName">
                  <Form.Label column sm="3">
                    Visitor Name
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control
                      type="name"
                      placeholder="Visitor Name"
                      value={visitorName}
                      onChange={(e) => setVisitorName(e.target.value)}
                    />
                  </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="mobile">
                  <Form.Label column sm="3">
                    Mobile No.
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control
                      type="number"
                      placeholder="Mobile No"
                      value={mobile}
                      onChange={(e) => setMobile(e.target.value)}
                    />
                  </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="fatherName">
                  <Form.Label column sm="3">
                    Father's Name
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control
                      type="name"
                      placeholder="Father Name"
                      value={fatherName}
                      onChange={(e) => setFatherName(e.target.value)}
                    />
                  </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="nid">
                  <Form.Label column sm="3">
                    NID
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control
                      type="number"
                      placeholder="NID"
                      value={nid}
                      onChange={(e) => setNid(e.target.value)}
                    />
                  </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="address">
                  <Form.Label column sm="3">
                    Address
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control
                      type="text"
                      placeholder="Address"
                      value={address}
                      onChange={(e) => setAddress(e.target.value)}
                    />
                  </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="profession">
                  <Form.Label column sm="3">
                    Profession
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control
                      type="text"
                      placeholder="Profession"
                      value={profession}
                      onChange={(e) => setProfession(e.target.value)}
                    />
                  </Col>
                </Form.Group>
              </Form.Group>
            </Col>
            <Col>
              <h2>Officer Detail</h2>
              <Form.Group>
                <Form.Group as={Row} controlId="officerName">
                  <Form.Label column sm="3">
                    Officer Name
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control
                      as="select"
                      value={officer}
                      onChange={(e) => setofficerName(e.target.value)}
                    >
                      <option>Select</option>
                      {officers.map((item) => (
                        <option key={item._id} value={item._id}>
                          {item.officerName}
                        </option>
                      ))}
                    </Form.Control>
                  </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="appointmentDate">
                  <Form.Label column sm="3">
                    Appoinment Date
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control
                      type="date"
                      value={appointmentDate}
                      onChange={(e) => setAppointmentdate(e.target.value)}
                    />
                  </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="appointmentTime">
                  <Form.Label column sm="3">
                    Appoinment Time
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control
                      type="time"
                      value={appointmentTime}
                      onChange={(e) => setAppointmentTime(e.target.value)}
                    />
                  </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="purpose">
                  <Form.Label column sm="3">
                    Purpose
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control
                      type="text"
                      placeholder="Purpose"
                      value={purpose}
                      onChange={(e) => setPurpose(e.target.value)}
                    />
                  </Col>
                </Form.Group>
                <Form.Group as={Row} controlId="Details">
                  <Form.Label column sm="3">
                    Details
                  </Form.Label>
                  <Col sm="9">
                    <Form.Control
                      as="textarea"
                      rows={3}
                      placeholder="Appointment Details"
                      value={details}
                      onChange={(e) => setDetails(e.target.value)}
                    />
                  </Col>
                </Form.Group>
                <Button variant="success" type="submit">
                  Create Request
                </Button>
              </Form.Group>
            </Col>
          </Row>
        </Form>
      </Container>
    </div>
  );
};

export default CreateNewRequestScreen;
