import React, { useState, useEffect } from "react";
import { Form, Button, Row, Col, Table } from "react-bootstrap";
import { getAppointmentByMobile } from "../actions/AppointmentAction";
import FormContainer from "../components/FormContainer";

const SearchAppointmentScreen = ({ history }) => {
  const [mobile, setMobile] = useState("");

  const [appointments, setAppointment] = useState([]);

  useEffect(() => {}, []);

  const detailsHandler = (id) => {
    history.push(`/appointmentDetails/${id}`);
  };

  let itemsToRender;
  var sl = 0;
  if (appointments) {
    itemsToRender = (
      <tbody className="text-center">
        {appointments.map((item) => (
          <tr key={item._id}>
            <td>{++sl}</td>
            <td>{item._id}</td>
            <td>{item.officer.officerName}</td>
            <td>{item.visitor.visitorName}</td>
            <td>{item.visitor.mobile}</td>
            <td>{item.status}</td>
            <td>
              <Button
                variant="outline-info"
                onClick={() => detailsHandler(item._id)}
              >
                {" "}
                Details
              </Button>
            </td>
          </tr>
        ))}
      </tbody>
    );
  }

  const submitHandler = async (e) => {
    e.preventDefault();
    try {
      await getAppointmentByMobile(mobile).then((response) => {
        setAppointment(response.data);
      });
    } catch (error) {
      //history.push("/newrequest", );
    }
  };

  return (
    <>
      <Row>
        <Col>
          <FormContainer>
            <Form onSubmit={submitHandler}>
              <Form.Group controlId="mobile">
                <Form.Label>Visitor Mobile No</Form.Label>
                <Form.Control
                  type="number"
                  placeholder="Enter Mobile No"
                  value={mobile}
                  onChange={(e) => setMobile(e.target.value)}
                ></Form.Control>
              </Form.Group>

              <Button type="submit" variant="outline-info">
                Search Appointments
              </Button>
            </Form>
          </FormContainer>
        </Col>
      </Row>

      <Row>
        <Col>
          <Table striped bordered hover responsive className="">
            <thead>
              <tr className="text-center">
                <th>SL</th>
                <th>Appointment ID</th>
                <th>Officer Name</th>
                <th>Visitor Name</th>
                <th>Visitor Mobile</th>
                <th>Status</th>
                <th>Action</th>
              </tr>
            </thead>
            {itemsToRender}
          </Table>
        </Col>
      </Row>
    </>
  );
};

export default SearchAppointmentScreen;
