import React, { useState, useEffect } from "react";
import { withRouter } from "react-router";
import { Form, Button} from "react-bootstrap";
import FormContainer from "./FormContainer";
import { userLogin } from "../actions/userAction";
import {LoggedUserData} from './ConstantData';
 
const OfficerLogin = ({ location, history }) => {
  const [mobile, setMobile] = useState("");
  const [password, setPassword] = useState("");

  const [userInfo, setUserInfo] = useState();

  useEffect(() => {
    setUserInfo(JSON.parse(localStorage.getItem(LoggedUserData)));
    if (userInfo) {
      history.push("/home");
    } else {
    }
  }, [history, userInfo]);

  const submitHandler = (e) => {
    e.preventDefault();
    userLogin("officer", mobile, password).then(() => {
      setUserInfo(JSON.parse(localStorage.getItem(LoggedUserData)));
      window.location.reload();
    });
  };
  return (
    <FormContainer>
      <h2 className="text-center">Officer's Login</h2>

      <Form onSubmit={submitHandler}>
        <Form.Group controlId="mobile">
          <Form.Label>Mobile No.</Form.Label>
          <Form.Control
            type="mobile"
            placeholder="Enter Mobile No."
            value={mobile}
            onChange={(e) => setMobile(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="password">
          <Form.Label>Password</Form.Label>
          <Form.Control
            type="password"
            placeholder="Enter Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          ></Form.Control>
        </Form.Group>
        <Button type="submit" variant="success">
          Sign In
        </Button>
      </Form>

      {/* <Row className="py-3">
        <Col>
          <a href="/VisitorRegister">New Visitor? Register</a>
        </Col>
      </Row> */}
    </FormContainer>
  );
};

export default withRouter(OfficerLogin);
