import React, { useState } from "react";
import { withRouter } from "react-router";
import { Form, Col, Button } from "react-bootstrap";
import FormContainer from "./FormContainer";
import { createUser } from "../actions/userAction";

const OfficerCreate = ({ history }) => {
  const [officerName, setOfficerName] = useState();
  const [mobile, setMobile] = useState();
  const [nid, setNid] = useState();
  const [password, setPassword] = useState();
  const [confirmPassword, setConfirmPassword] = useState();
//  const [uploading, setUploading] = useState(false);

  // const uploadFileHandler = (e) => {
  //   uploadFile(e, setAddress, setUploading);
  // };

  const createUserHandler = (e) => {
    e.preventDefault();
    if (password !== confirmPassword) {
      alert("Password not matched");
    } else {
      if (window.confirm("Are you sure to Submit ?")) {
        createUser({
          officerName,
          mobile,
          nid,
          password,
        },"officer").then(() => {
          window.location.reload();
          history.push("/");
        });
      }
    }
  };

  let itemsToRender;

  if (true) {
    itemsToRender = (
      <Form onSubmit={createUserHandler}>
          <Form.Group controlId="officerName">
            <Form.Label>Officer Name</Form.Label>
            <Form.Control
              type="name"
              placeholder="Officer Name"
              value={officerName}
              onChange={(e) => setOfficerName(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Form.Group controlId="mobile">
            <Form.Label>Mobile No</Form.Label>
            <Form.Control
              type="number"
              placeholder="Mobile No"
              value={mobile}
              onChange={(e) => setMobile(e.target.value)}
            ></Form.Control>
          </Form.Group>

        <Form.Group controlId="nid">
          <Form.Label>NID</Form.Label>
          <Form.Control
            type="number"
            placeholder="NID"
            value={nid}
            onChange={(e) => setNid(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Row>
          <Form.Group as={Col} controlId="password">
            <Form.Label>Password</Form.Label>
            <Form.Control
              type="password"
              placeholder="Enter Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            ></Form.Control>
          </Form.Group>
          <Form.Group as={Col} controlId="confirmPassword">
            <Form.Label>Confirm Password</Form.Label>
            <Form.Control
              type="password"
              placeholder="Confirm Password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
            ></Form.Control>
          </Form.Group>
        </Form.Row>

        <Button type="submit" variant="primary">
          Sign Up
        </Button>
      </Form>
    );
  }

  return (
    <>
      <FormContainer>{itemsToRender}</FormContainer>
    </>
  );
};

export default withRouter(OfficerCreate);
