import React, { useEffect, useState } from "react";
import { Table, Button, Row, Col } from "react-bootstrap";
import { listUsers } from "../actions/userAction";
import Loading from "../components/Loading";

const OfficerListScreen = ({ history }) => {
  const [loading, setLoading] = useState(true);
  const [users, setUsers] = useState([]);

  useEffect(() => {
    const listT = async () => {
      try {
        await listUsers("officer").then(response =>{
          setLoading(false);
          setUsers(response.data);
        });
        
      } catch (error) {}
    };

    listT();
  }, []);

  const detailsHandler = (id) => {
    //history.push(`/appointmentDetails/${id}`)
  };

  let itemsToRender;
  var sl = 0;
  if (users) {
    itemsToRender = (
      <tbody className="text-center">
        {users.map((item) => (
          <tr key={item._id}>
            <td>{++sl}</td>
            <td>{item._id}</td>
            <td>{item.officerName}</td>
            <td>{item.mobile}</td>
            <td>{item.nid}</td>
            <td>
              <Button
                variant="outline-info"
                onClick={() => detailsHandler(item._id)}
              >
                {" "}
                Details
              </Button>
            </td>
          </tr>
        ))}
      </tbody>
    );
  }

  return (
    <>
      <Row className="align-items-center">
        <Col>
          <h2 className="text-center">Officer List</h2>
        </Col>
      </Row>
      {loading ? (
        <Loading />
      ) : (
        <Table striped bordered hover responsive className="">
          <thead>
            <tr className="text-center">
              <th>SL</th>
              <th>Officer ID</th>
              <th>Officer Name</th>
              <th>Mobile</th>
              <th>Nid</th>
              <th>Action</th>
            </tr>
          </thead>
          {itemsToRender}
        </Table>
      )}
    </>
  );
};

export default OfficerListScreen;
