import axios from "axios";
import { tokenConfig } from "./tokenConfig";


export const sortedAppointmentList = async (status) => {
  try {
    return await axios.get(
      `/api/appointment/sortList/${status}`,
      tokenConfig()
    );
  } catch (error) {}
  return;
};

export const getAppointmentById = async (id) => {
  try {
    return await axios.get(`/api/appointment/details/${id}`);
  } catch (error) {}
};

export const deleteAppointment = async (id) => {
  try {
    return await axios.delete(`/api/appointment/details/${id}`);
  } catch (error) {}
};

export const createAppointment = async (appointment) => {
  try {
    return await axios
    .post(`/api/appointment/create`, appointment)
    .then((response) => {
      console.log("Appointment Created.........");
    });
  } catch (error) {
    console.log(appointment);
  }
};

export const getOfficerName = async (id) => {
  try {
    return await axios.get(`/api/users/officerName`);
  } catch (error) {}
};

export const changeStatus = async (id, status) => {
  try {
    return await axios.put(`/api/appointment/details/${id}/${status}`);
  } catch (error) {}
};

export const getAppointmentByMobile = async (keyword = "") => {
  try {
    return await axios.get(`/api/appointment/appointmentList?keyword=${keyword}`);
  } catch (error) {}
};

