import axios from "axios";
import { tokenConfig } from "./tokenConfig";
import { LoggedUserData } from "../components/ConstantData";

export const userLogin = async (userType, mobile, password) => {
  try {
    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    const { data } = await axios.post(
      `/api/users/${userType}/login`,
      {
        mobile,
        password,
      },
      config
    );

    localStorage.setItem(LoggedUserData, JSON.stringify(data));
  } catch (error) {}
};

export const createUser = (user, userType) => {
  try {
    return axios.post(`/api/users/${userType}`, user);
  } catch (error) {}
};

export const listUsers = async (userType) => {
  try {
    return await axios.get(`api/users/${userType}`, tokenConfig());
  } catch (error) {}
};

export const getUserByMobile = async (mobile) => {
  try {
    return await axios.get(
      `/api/users/visitor/${mobile}/details`,
      tokenConfig()
    );
  } catch (error) {
    console.log("error hyse");
  }
};

