import React, { useEffect, useState } from "react";
import { Table, Row, Col, Button } from "react-bootstrap";
import {
  getAppointmentById,
  changeStatus,
  deleteAppointment,
} from "../actions/AppointmentAction";
import CardModal from "../components/CardModal";

const AppointmentDetailsScreen = ({ match, history }) => {
  const [appointment, setAppointment] = useState();

  useEffect(() => {
    const listT = async () => {
      const { data } = await getAppointmentById(match.params.id);
      setAppointment(data);
    };

    listT().then(() => {
      //setOfficerName(appointment.officer)
    });
  }, [match]);

  const statusHandler = (sts) => {
    if (window.confirm("Are you sure ?")) {
      changeStatus(match.params.id, sts).then(() => {
        window.location.reload();
      });
    }
  };

  const deleteHandler = () => {
    if (window.confirm("Are you sure to delete ?")) {
      deleteAppointment(match.params.id).then(() => {
        history.push("/sortedAppointment/Pending");
      });
    }
  };

  let itemsToRender;
  if (appointment) {
    itemsToRender = (
      <tbody className="">
        <tr>
          <td>Appointment Id:</td>
          <td>{appointment._id}</td>
        </tr>
        <tr>
          <td>Visitor Name:</td>
          <td>{appointment.visitor.visitorName}</td>
        </tr>
        {appointment.visitor.cardNo ? (
          <tr>
            <td>Card No:</td>
            <td>{appointment.visitor.cardNo}</td>
          </tr>
        ) : (
          <> </>
        )}
        <tr>
          <td>Visitor Mobile:</td>
          <td>{appointment.visitor.mobile}</td>
        </tr>
        <tr>
          <td>Officer Name:</td>
          <td>{appointment.officer.officerName}</td>
        </tr>
        <tr>
          <td>Date:</td>
          <td>{appointment.date}</td>
        </tr>
        <tr>
          <td>Time:</td>
          <td>{appointment.time}</td>
        </tr>
        <tr>
          <td>Priority:</td>
          <td>{appointment.priority}</td>
        </tr>
        <tr>
          <td>Purpose:</td>
          <td>{appointment.purpose}</td>
        </tr>
        <tr>
          <td>Details:</td>
          <td>{appointment.details}</td>
        </tr>
        <tr>
          <td>Status:</td>
          <td>{appointment.status}</td>
        </tr>
        <tr>
          <td>Action:</td>
          <td>
            {appointment.status === "Pending" ? (
              <>
                <Button
                  variant="outline-success"
                  onClick={() => statusHandler("approved")}
                >
                  Approved
                </Button>
                <Button
                  variant="outline-warning"
                  onClick={() => statusHandler("reject")}
                >
                  Reject
                </Button>
              </>
            ) : (
              <>
                <Button
                  variant="outline-danger"
                  onClick={() => deleteHandler()}
                >
                  Delete Appointment
                </Button>

                {appointment.visitor.cardNo ? (
                  <>
                    {" "}
                    <Button variant="outline-info">Card Assigned</Button>
                  </>
                ) : (
                  <>
                    <CardModal visitorId={appointment.visitor._id} />
                  </>
                )}
              </>
            )}
          </td>
        </tr>
        <tr></tr>
      </tbody>
    );
  }

  return (
    <>
      <Row className="align-items-center">
        <Col>
          <h1>Details</h1>
        </Col>
        <Col className="text-right"></Col>
      </Row>
      <Row>
        <Col md lg="2"></Col>
        <Col md lg="8">
          <Table striped bordered hover responsive className="">
            <thead className=""></thead>
            {itemsToRender}
          </Table>
        </Col>
        <Col md lg="2"></Col>
      </Row>
    </>
  );
};

export default AppointmentDetailsScreen;
