import axios from "axios";
import { tokenConfig } from "./tokenConfig";

export const assignedCard = async (cardNo, visitorId) => {
  try {
    return await axios.put(
      `/api/accesscard/assignedCard`,
      { cardNo, visitorId },
      tokenConfig()
    );
  } catch (error) {}
};

export const unassignedCard = async (cardNo) => {
  try {
    return await axios.put(
      `/api/accesscard/cancelCard`,
      { cardNo },
      tokenConfig()
    );
  } catch (error) {}
};

export const getCardList = async () => {
  try {
    return await axios.get("/api/accesscard/cardList", tokenConfig());
  } catch (error) {}
};
