import React, { useState } from "react";
import { withRouter } from "react-router";
import { Form, Col, Button } from "react-bootstrap";
import FormContainer from "./FormContainer";
import { createUser } from "../actions/userAction";

const VisitorCreate = ({ history }) => {
  const [visitorName, setVisitorName] = useState();
  const [mobile, setMobile] = useState();
  const [fatherName, setFatherName] = useState();
  const [nid, setNid] = useState();
  const [address, setAddress] = useState();
  const [profession, setProfession] = useState();
  const [password, setPassword] = useState();
  const [confirmPassword, setConfirmPassword] = useState();
//  const [uploading, setUploading] = useState(false);

  // const uploadFileHandler = (e) => {
  //   uploadFile(e, setAddress, setUploading);
  // };

  const createUserHandler = (e) => {
    e.preventDefault();
    if (password !== confirmPassword) {
      alert("Password not matched");
    } else {
      if (window.confirm("Are you sure to Submit ?")) {
        createUser({
          visitorName,
          mobile,
          fatherName,
          nid,
          address,
          profession,
          password,
        },"visitor").then(() => {
          history.push("/");
        });
      }
    }
  };

  let itemsToRender;

  if (true) {
    itemsToRender = (
      <Form onSubmit={createUserHandler}>
          <Form.Group controlId="visitorName">
            <Form.Label>Visitor Name</Form.Label>
            <Form.Control
              type="name"
              placeholder="Visitor Name"
              value={visitorName}
              onChange={(e) => setVisitorName(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Form.Group controlId="mobile">
            <Form.Label>Mobile No</Form.Label>
            <Form.Control
              type="number"
              placeholder="Mobile No"
              value={mobile}
              onChange={(e) => setMobile(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Form.Group controlId="fatherName">
            <Form.Label>Father Name</Form.Label>
            <Form.Control
              type="name"
              placeholder="Father Name"
              value={fatherName}
              onChange={(e) => setFatherName(e.target.value)}
            ></Form.Control>
          </Form.Group>
        <Form.Group controlId="nid">
          <Form.Label>NID</Form.Label>
          <Form.Control
            type="number"
            placeholder="NID"
            value={nid}
            onChange={(e) => setNid(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="address">
          <Form.Label>Address</Form.Label>
          <Form.Control
            type="text"
            placeholder="Address"
            value={address}
            onChange={(e) => setAddress(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Group controlId="profession">
          <Form.Label>Profession</Form.Label>
          <Form.Control
            type="text"
            placeholder="Profession"
            value={profession}
            onChange={(e) => setProfession(e.target.value)}
          ></Form.Control>
        </Form.Group>

        <Form.Row>
          <Form.Group as={Col} controlId="password">
            <Form.Label>Password</Form.Label>
            <Form.Control
              type="password"
              placeholder="Enter Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            ></Form.Control>
          </Form.Group>
          <Form.Group as={Col} controlId="confirmPassword">
            <Form.Label>Confirm Password</Form.Label>
            <Form.Control
              type="password"
              placeholder="Confirm Password"
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
            ></Form.Control>
          </Form.Group>
        </Form.Row>

        <Button type="submit" variant="primary">
          Sign Up
        </Button>
      </Form>
    );
  }

  return (
    <>
      <FormContainer>{itemsToRender}</FormContainer>
    </>
  );
};

export default withRouter(VisitorCreate);
