import React, { useState, useEffect } from "react";
import { Form, Button } from "react-bootstrap";
// import { createTask } from "../actions/taskAction";
import FormContainer from "../components/FormContainer";
import { PriorityData } from "../components/ConstantData";
import {
  createAppointment,
  getOfficerName,
} from "../actions/AppointmentAction";

const CreateAppointmentScreen = ({ history }) => {
  const userInfo = JSON.parse(localStorage.getItem("VmsUserData"));

  const [officer, setofficerName] = useState();
  const [appointmentDate, setAppointmentdate] = useState("");
  const [appointmentTime, setAppointmentTime] = useState("");
  const [priority, setPriority] = useState("");
  const [purpose, setPurpose] = useState("");
  const [details, setDetails] = useState("");
  const [officers, setOfficers] = useState([]);

  useEffect(() => {
    const listU = async () => {
      const { data } = await getOfficerName();
      setOfficers(data);
    };
    listU();
  }, [userInfo]);

  const submitHandler = (e) => {
    e.preventDefault();
    console.log("creating....");
    createAppointment({
      visitor: userInfo._id,
      officer,
      date: appointmentDate,
      time: appointmentTime,
      priority,
      purpose,
      details,
    }).then(() => {
      history.push("/sortedAppointment/Pending");
    });
  };

  return (
    <div>
      <FormContainer>
        <Form onSubmit={submitHandler}>
          <Form.Group controlId="officerName">
            <Form.Label>Officer Name</Form.Label>
            <Form.Control
              as="select"
              value={officer}
              onChange={(e) => setofficerName(e.target.value)}
            >
              <option>Select</option>
              {officers.map((item) => (
                <option key={item._id} value={item._id}>
                  {item.officerName}
                </option>
              ))}
            </Form.Control>
          </Form.Group>

          <Form.Group controlId="appointmentDate">
            <Form.Label>Appointment Date</Form.Label>
            <Form.Control
              type="date"
              value={appointmentDate}
              onChange={(e) => setAppointmentdate(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Form.Group controlId="appointmentTime">
            <Form.Label>Appointment Time</Form.Label>
            <Form.Control
              type="time"
              value={appointmentTime}
              onChange={(e) => setAppointmentTime(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Form.Group controlId="priority">
            <Form.Label>Priority</Form.Label>
            <Form.Control
              as="select"
              value={priority}
              onChange={(e) => setPriority(e.target.value)}
            >
              <option>Select</option>
              {PriorityData.map((item) => (
                <option key={item} value={item}>
                  {item}
                </option>
              ))}
            </Form.Control>
          </Form.Group>

          <Form.Group controlId="purpose">
            <Form.Label>Purpose</Form.Label>
            <Form.Control
              type="text"
              placeholder="Purpose"
              value={purpose}
              onChange={(e) => setPurpose(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Form.Group controlId="Details">
            <Form.Label>Details</Form.Label>
            <Form.Control
              as="textarea"
              rows={3}
              placeholder="Appointment Details"
              value={details}
              onChange={(e) => setDetails(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Button type="submit" variant="success">
            Submit Appointment
          </Button>
        </Form>
      </FormContainer>
    </div>
  );
};

export default CreateAppointmentScreen;
