import React, { useEffect, useState } from "react";
import { Nav, Navbar, NavDropdown, Button } from "react-bootstrap";
import { withRouter } from "react-router";
import { LoggedUserData } from "./ConstantData";
import { LinkContainer } from "react-router-bootstrap";

const Header = ({ history }) => {
  const [userInfo] = useState(JSON.parse(localStorage.getItem(LoggedUserData)));
  const [redirect, setRedirect] = useState("/");

  useEffect(() => {
    if (!userInfo) {
      history.push(redirect);
    }
  }, [history, userInfo, redirect]);

  const logoutHandler = () => {
    localStorage.removeItem(LoggedUserData);
    window.location.reload();
  };

  const redirectHandler = () => {
    setRedirect("/officerLogin");
    history.push("/officerLogin");
  };

  return (
    <>
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Navbar.Brand href="/">
          <img
            src="https://secureservercdn.net/72.167.242.48/137.171.myftpupload.com/wp-content/uploads/2021/01/SEBPO_LOGO_2021.png"
            width="125"
            className="d-inline-block align-top"
            alt="SEBPO"
          />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          {userInfo ? (
                <>
              <Nav className="mr-auto">
                <LinkContainer to="/home">
                  <Nav.Link>Dashboard</Nav.Link>
                </LinkContainer>
                <LinkContainer to="/cardList">
                  <Nav.Link>Card List</Nav.Link>
                </LinkContainer>
                <NavDropdown title="Appointments" id="collasible-nav-dropdown">
                  <NavDropdown.Item href="/newappointment">
                    New Appointment
                  </NavDropdown.Item>
                  <NavDropdown.Item href="/reqnewappointment">
                    New Request
                  </NavDropdown.Item>
                  <NavDropdown.Divider />
                  <LinkContainer to="/sortedAppointment/Pending">
                    <NavDropdown.Item>Pending Appointment</NavDropdown.Item>
                  </LinkContainer>
                  <LinkContainer to="/sortedAppointment/Approved">
                    <NavDropdown.Item>Approved Appointment</NavDropdown.Item>
                  </LinkContainer>
                  <LinkContainer to="/sortedAppointment/Reject">
                    <NavDropdown.Item>Rejected Appointment</NavDropdown.Item>
                  </LinkContainer>
                </NavDropdown>
                <NavDropdown title='Officer' id="collasible-nav-dropdown">
                  <LinkContainer to="/newOfficerCreate">
                    <NavDropdown.Item>New Officer</NavDropdown.Item>
                  </LinkContainer>
                  <NavDropdown.Divider />
                  <LinkContainer to="/officerList">
                    <NavDropdown.Item>Officer List</NavDropdown.Item>
                  </LinkContainer>
                </NavDropdown>
                <NavDropdown title='Visitor' id="collasible-nav-dropdown">
                  <LinkContainer to="/newVisitorCreate">
                    <NavDropdown.Item>New Visitor</NavDropdown.Item>
                  </LinkContainer>
                  <NavDropdown.Divider />
                  <LinkContainer to="/visitorList">
                    <NavDropdown.Item>Visitor List</NavDropdown.Item>
                  </LinkContainer>
                </NavDropdown>
              </Nav>
              <Nav>
                <NavDropdown
                  title={
                    userInfo.visitorName
                      ? userInfo.visitorName
                      : userInfo.officerName
                  }
                  id="username"
                >
                  <NavDropdown.Item href="/profile">Profile</NavDropdown.Item>
                  <NavDropdown.Item onClick={logoutHandler}>
                    Logout
                  </NavDropdown.Item>
                </NavDropdown>
              </Nav>
            </>
          ) : (
            <>
              <Nav className="ml-auto">
                <Button
                  variant="outline-warning"
                  onClick={() => redirectHandler()}
                >
                  Officer Login
                </Button>
              </Nav>
            </>
          )}
        </Navbar.Collapse>
      </Navbar>
      <br />
    </>
  );
};

export default withRouter(Header);
