import React, { useEffect, useState } from "react";
import { Table, Button, Row, Col } from "react-bootstrap";
import { sortedAppointmentList } from "../actions/AppointmentAction";
import Loading from "../components/Loading";

const SortedListScreen = ({ match, history }) => {
  const [loading, setLoading] = useState(true);
  const [appointments, setAppointment] = useState([]);

  useEffect(() => {
    const listT = async () => {
      try {
        await sortedAppointmentList(match.params.status).then((response) => {
          setLoading(false);
          setAppointment(response.data);
        });
      } catch (error) {}
    };

    listT();
  }, [match]);

  const detailsHandler = (id) => {
    history.push(`/appointmentDetails/${id}`);
  };

  let itemsToRender;
  var sl = 0;
  if (appointments) {
    itemsToRender = (
      <tbody className="text-center">
        {appointments.map((item) => (
          <tr key={item._id}>
            <td>{++sl}</td>
            <td>{item._id}</td>
            <td>{item.officer.officerName}</td>
            <td>{item.visitor.visitorName}</td>
            <td>{item.visitor.mobile}</td>
            <td>{item.status}</td>
            <td>
              {/* <LinkContainer to={`/item/${item._id}/details`}>
                <Button variant="dark" className="btn-sm">
                  <i className="fas fa-item-diagram"></i>
                </Button>
              </LinkContainer>
              <LinkContainer to={`/project/${project._id}/edit`}>
                <Button variant="light" className="btn-sm">
                  <i className="fas fa-edit"></i>
                </Button>
              </LinkContainer> */}
              <Button
                variant="outline-info"
                onClick={() => detailsHandler(item._id)}
              >
                {" "}
                Details
              </Button>
            </td>
          </tr>
        ))}
      </tbody>
    );
  }

  return (
    <>
      <Row className="align-items-center">
        <Col>
          <h1>{match.params.status} List</h1>
        </Col>
        <Col className="text-right"></Col>
      </Row>
      {loading ? (
        <Loading />
      ) : (
        <Row>
          <Col>
            <Table striped bordered hover responsive className="">
              <thead>
                <tr className="text-center">
                  <th>SL</th>
                  <th>Appointment ID</th>
                  <th>Officer Name</th>
                  <th>Visitor Name</th>
                  <th>Visitor Mobile</th>
                  <th>Status</th>
                  <th>Action</th>
                </tr>
              </thead>
              {itemsToRender}
            </Table>
          </Col>
        </Row>
      )}
    </>
  );
};

export default SortedListScreen;
