import React, { useState } from "react";
import { Form, Button } from "react-bootstrap";
import { getUserByMobile } from "../actions/userAction";
import FormContainer from "../components/FormContainer";

const ReqNewAppointment = ({ history }) => {
  const [mobile, setMobile] = useState("");

  const submitHandler = async (e) => {
    e.preventDefault();
    try {
      await getUserByMobile(mobile).then((response) => {
        if (response.data) {
          history.push(`/newrequest/${mobile}`, response.data);
        } else {
          history.push(`/newrequest/${mobile}`);
        }
      });
    } catch (error) {
      //history.push("/newrequest", );
    }
  };

  return (
    <div>
      <FormContainer>
        <Form onSubmit={submitHandler}>
          <Form.Group controlId="mobile">
            <Form.Label>Visitor Mobile No</Form.Label>
            <Form.Control
              type="number"
              placeholder="Enter Mobile No"
              value={mobile}
              onChange={(e) => setMobile(e.target.value)}
            ></Form.Control>
          </Form.Group>

          <Button type="submit" variant="success">
            Search
          </Button>
        </Form>
      </FormContainer>
    </div>
  );
};

export default ReqNewAppointment;
