import React, { useEffect, useState } from "react";
import { Table, Button, Row, Col } from "react-bootstrap";
import { getCardList, unassignedCard } from "../actions/cardAction";
import Loading from "../components/Loading";

const CardListScreen = ({ history }) => {
  const [loading, setLoading] = useState(true);
  const [cards, setCards] = useState([]);

  useEffect(() => {
    const listT = async () => {
      try {
        await getCardList().then((response) => {
          setLoading(false);
          setCards(response.data);
        });
      } catch (error) {}
    };

    listT();
  }, []);

  const cancelCardHandler = (cardNo) => {
    if (window.confirm(`Are you sure to unassigned cardNo: ${cardNo} ?`)) {
      unassignedCard(cardNo).then(() => {
        window.location.reload();
      });
    }
    //history.push(`/appointmentDetails/${id}`)
  };

  let itemsToRender;
  var sl = 0;
  if (cards) {
    itemsToRender = (
      <tbody className="text-center">
        {cards.map((item) => (
          <tr key={item._id}>
            <td>{++sl}</td>
            <td>{item.cardNo}</td>
            {item.isAssigned ? <td>Assigned</td> : <td>Not Assigned</td>}
            {item.visitor ? (
              <td>{item.visitor.visitorName}</td>
            ) : (
              <td>- - - - -</td>
            )}
            {item.visitor ? <td>{item.visitor.mobile}</td> : <td>- - - - -</td>}
            <td>
              {item.isAssigned ? (
                <Button
                  variant="outline-danger"
                  onClick={() => cancelCardHandler(item.cardNo)}
                >
                  Unassign
                </Button>
              ) : (
                <Button variant="outline-success">Not Used</Button>
              )}
            </td>
          </tr>
        ))}
      </tbody>
    );
  }

  return (
    <>
      <Row className="align-items-center">
        <Col>
          <h2 className="text-center">Card List</h2>
        </Col>
      </Row>
      {loading ? (
        <Loading />
      ) : (
        <Table striped bordered hover responsive className="">
          <thead>
            <tr className="text-center">
              <th>SL</th>
              <th>Card No</th>
              <th>Status</th>
              <th>Assigned To</th>
              <th>Mobile</th>
              <th>Action</th>
            </tr>
          </thead>
          {itemsToRender}
        </Table>
      )}
    </>
  );
};

export default CardListScreen;
