import "./bootstrap.min.css";
import React from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import Header from "./components/Header";
//import HomeScreen from "./screens/HomeScreen";
import CreateAppointment from "./screens/CreateAppointmentScreen";
import SortedListScreen from "./screens/SortedListScreen";
import AppointmentDetailsScreen from "./screens/AppointmentDetailsScreen";
import OfficerAuthScreen from "./screens/OfficerAuthScreen";
import UserAuthScreen from "./screens/UserAuthScreen";
import OfficerListScreen from "./screens/OfficerListScreen";
import VisitorListScreen from "./screens/VisitorListScreen";
import ProfileScreen from "./screens/ProfileScreen";
import VisitorCreate from "./components/VisitorCreate";
import OfficerCreate from "./components/OfficerCreate";
import CreateNewRequestScreen from "./screens/CreateNewRequestScreen";
import ReqNewAppointment from "./screens/ReqNewAppointment";
import SearchAppointmentScreen from "./screens/SearchAppointmentScreen";
import CardListScreen from "./screens/CardListScreen";

function App() {
  return (
    <Router>
      <div className="container">
        <Header />
        <main className="py-3">
          <div className="row ">
            <div className="col-md-12">
              <Route exact path="/home" component={SearchAppointmentScreen} />
              <Route exact path="/" component={UserAuthScreen} />
              <Route exact path="/newVisitorCreate" component={VisitorCreate} />
              <Route exact path="/newOfficerCreate" component={OfficerCreate} />
              <Route exact path="/officerLogin" component={OfficerAuthScreen} />
              <Route exact path="/officerList" component={OfficerListScreen} />
              <Route exact path="/visitorList" component={VisitorListScreen} />
              <Route exact path="/cardList" component={CardListScreen} />
              <Route
                exact
                path="/newappointment"
                component={CreateAppointment}
              />
              <Route
                exact
                path="/sortedAppointment/:status"
                component={SortedListScreen}
              />
              <Route
                exact
                path="/appointmentDetails/:id"
                component={AppointmentDetailsScreen}
              />
              <Route exact path="/profile" component={ProfileScreen} />
              <Route
                exact
                path="/reqnewappointment"
                component={ReqNewAppointment}
              />
              <Route
                exact
                path="/newrequest/:mobile"
                component={CreateNewRequestScreen}
              />
            </div>
          </div>
          <div className="row"></div>
        </main>
      </div>
    </Router>
  );
}

export default App;
